import React, { useState } from "react";
import { ReactComponent as Arrow } from "../../../assets/svg/arrow.svg";
import "./Selector.scss";
import Button from "../../UI/BUTTONS/button";
import { connect } from "react-redux";
import { addItemCart } from "../../../store/actions";
import { motion } from "framer-motion";

const Selector = ({ item, addItemCart }) => {
  const { stock, name, price, color, description, itemId, itemPicture } = item;
  const [isDropActive, isDropActiveHandler] = useState(false);
  const [dropStatus, dropStatusHandler] = useState("SELECCIONA UN TALLE");
  const transition = {
    duration: 1,
    ease: [0.43, 0.13, 0.23, 0.96]
  };

  const imageVariants = {
    exit: { y: "50%", opacity: 0, transition },
    enter: {
      y: "0%",
      opacity: 1,
      transition
    }
  };

  const backVariants = {
    exit: { x: 100, opacity: 0, transition },
    enter: { x: 0, opacity: 1, transition: { delay: 0.5, ...transition } }
  };
  return (
    <motion.div className='selector' variants={backVariants}>
      <h1>{name}</h1>
      <h4>${price}</h4>
      <div className='size-drop' onClick={() => isDropActiveHandler(!isDropActive)}>
        {dropStatus}
        <Arrow className={`arrow ${isDropActive ? "active" : ""}`} />
        <div className='size-drop__list' className={`size-drop__list ${isDropActive ? "active" : ""}`}>
          {stock.map(item => {
            let status = "";
            if (item.stock === 0) status = "Sin Stock";
            if (item.stock > 0 && item.stock <= 3) status = `Ultimos ${item.stock}!`;
            return (
              <div
                className={`size-drop__list--item ${item.stock === 0 ? "disabled" : ""}`}
                onClick={() => {
                  if (item.stock > 0) dropStatusHandler(item.size);
                }}
              >
                <span>{item.size}</span>
                {status ? <span>{status}</span> : null}
              </div>
            );
          })}
        </div>
      </div>
      <Button
        style={{ marginTop: "32px" }}
        onClick={() => {
          if (dropStatus !== "SELECCIONA UN TALLE") {
            addItemCart({ ...item, size: dropStatus });
          } else {
            //ANIMATION
          }
        }}
      >
        Agregar al Carrito
      </Button>
      <div className='info-container'>
        <p>{description[0].toUpperCase()}</p>
        <p>MATERIAL - 100% ALGODON</p>
        <p>COLOR - {color.toUpperCase()}</p>
      </div>
    </motion.div>
  );
};

export default connect(null, { addItemCart })(Selector);
