import React from "react";
import "./Showcase.scss";
import { connect } from "react-redux";
import Carousel from "../Carousel/Carousel";
import Selector from "../Selector/Selector";
import { motion } from "framer-motion";

export const Showcase = ({ currentItem }) => {
  const transition = {
    duration: 1,
    ease: [0.43, 0.13, 0.23, 0.96]
  };

  const imageVariants = {
    exit: { y: "50%", opacity: 0, transition },
    enter: {
      y: "0%",
      opacity: 1,
      transition
    }
  };

  const backVariants = {
    exit: { x: 100, opacity: 0, transition },
    enter: { x: 0, opacity: 1, transition: { delay: 0.5, ...transition } }
  };

  console.log(currentItem);
  const imgArray = [currentItem.itemPicture.front, currentItem.itemPicture.back];
  return (
    <motion.div className='item-showcase' initial='exit' animate='enter' exit='exit'>
      <Carousel imgArray={imgArray} item={currentItem} />
      <Selector item={currentItem} />
    </motion.div>
  );
};

const mapStateToProps = state => {
  return {
    currentItem: state.general.currentItem
  };
};

export default connect(mapStateToProps)(Showcase);
