import React, { useState, useRef, createRef, useEffect } from "react";
import { ButtonBack, ButtonFirst, ButtonLast, ButtonNext, CarouselProvider, DotGroup, Dot, ImageWithZoom, Image, Slide, Slider } from "pure-react-carousel";
import "./Carousel.scss";
import { ReactComponent as Next } from "../../../assets/svg/next.svg";
import { ReactComponent as Prev } from "../../../assets/svg/back.svg";
import { motion } from "framer-motion";
import "pure-react-carousel/dist/react-carousel.es.css";
const Carousel = ({ imgArray, item }) => {
  const [activeDot, activeDotHandler] = useState(0);
  // const imgArray = [Front, Back];
  const transition = {
    duration: 1,
    ease: [0.43, 0.13, 0.23, 0.96]
  };
  const imageVariants = {
    exit: { y: "50%", opacity: 0, transition },
    enter: {
      y: "0%",
      opacity: 1,
      transition
    }
  };
  return (
    <motion.div className='car' variants={imageVariants}>
      <CarouselProvider naturalSlideWidth={1000} naturalSlideHeight={1200} totalSlides={imgArray.length} visibleSlides={1} lockOnWindowScroll={true}>
        <Slider style={{ width: "100%", backgroundColor: "#d9d9d9" }}>
          {imgArray.map((img, index) => {
            return (
              <Slide index={index} className='slideeee'>
                <Image src={img} alt='img' />
              </Slide>
            );
          })}
        </Slider>
        <ButtonBack className='carousel-actions back'>
          <Prev />
        </ButtonBack>
        <ButtonNext className='carousel-actions next'>
          <Next />
        </ButtonNext>
        {/* <div className='dot-container'>
          {imgArray.map((img, index) => (
            <Dot className={`dot-item ${activeDot === index ? "active" : ""}`} slide={index} onClick={() => activeDotHandler(index)} />
          ))}
        </div> */}
      </CarouselProvider>
    </motion.div>
  );
};

const Carousel2 = ({ imgArray }) => {
  const elementsRef = useRef(imgArray.map(() => createRef()));
  const carousel = useRef(null);
  const [carouselComp, manageCarousel] = useState(null);
  const [focusImage, focusImageHandler] = useState(0);

  useEffect(() => {
    let myElement = document.querySelector(".carousel");
    myElement.scrollTo(0, 250);
    manageCarousel(myElement);
  }, []);
  const transition = {
    duration: 1,
    ease: [0.43, 0.13, 0.23, 0.96]
  };
  const imageVariants = {
    exit: { y: "50%", opacity: 0, transition },
    enter: {
      y: "0%",
      opacity: 1,
      transition
    }
  };

  console.log("REFS");
  console.log(elementsRef);
  // const scrollToRef = ref => carouselComp.scrollTo(0, ref.current.offsetTop + 200);
  const scrollToRef = ref =>
    carouselComp.scrollTo({
      top: ref.current.offsetTop + 100,
      behavior: "smooth"
    });
  console.log(carouselComp);

  return (
    <motion.div className='carousel-container' variants={imageVariants}>
      <div className='mini'>
        {imgArray.map((img, index) => {
          return (
            <div
              className={`mini-item ${focusImage === index ? "visible" : ""}`}
              onClick={() => {
                focusImageHandler(index);
                scrollToRef(elementsRef.current[index]);
              }}
            >
              <img src={img} alt='img' />
            </div>
          );
        })}
      </div>

      <div className='carousel' id='carousel' ref={carousel}>
        {imgArray.map((img, index) => {
          return (
            <div index={index} className='carousel-item' ref={elementsRef.current[index]}>
              <img src={img} alt='img' />
            </div>
          );
        })}
      </div>
    </motion.div>
  );
};

export default Carousel;
