import React, { Component } from "react";
import "./Item.scss";
import Nav from "../../components/Nav/Nav";
import Showcase from "../../components/ItemPage/Showcase/Showcase";
import Footer from "../../components/Footer/Footer";
import { connect } from "react-redux";
import { getItem } from "../../store/actions/generalActions";

class Home extends Component {
  componentDidMount() {
    window.scrollTo({
      top: 0,
    });
    this.props.getItem(this.props.match.params.id, this.props.history);
  }
  render() {
    return (
      <>
        <Nav />
        <Showcase />
        <Footer />
      </>
    );
  }
}

export default connect(null, { getItem })(Home);
