import React, { useEffect } from "react";
import { Route, Switch } from "react-router-dom";
import Home from "./pages/Home/Home";
import Item from "./pages/Item/Item";
import Checkout from "./pages/Checkout/Checkout";
import Store from "./pages/Store/Store";
import LookBook from "./pages/Lookbook/Lookbook";
import FormPage from "./pages/FormPage//FormPage";
import { AnimatePresence } from "framer-motion";
import { connect } from "react-redux";
import { calculatePrice } from "./store/actions";
import StatusPage from "./pages/STATUS/statusPage";
const App = ({ calculatePrice, ...props }) => {
  useEffect(() => {
    calculatePrice();
  });
  return (
    <div className='App'>
      <Route exact path='/item/:id' component={Item} />
      <Route exact path='/store' component={Store} />

      <Route exact path='/lookbook' component={LookBook} />
      <Route exact path='/paymentform' component={FormPage} />
      <Route exact path='/checkout' component={Checkout} />

      <Route path='/success' render={(routeProps) => <StatusPage {...routeProps} type='success' />} />
      <Route path='/pend' render={(routeProps) => <StatusPage {...routeProps} type='pend' />} />
      <Route path='/fail' render={(routeProps) => <StatusPage {...routeProps} type='fail' />} />

      <Route exact path='/' component={Home} />

      {/* </AnimatePresence> */}
    </div>
  );
};

export default connect(null, { calculatePrice })(App);
