import React, { useState } from "react";
import "./ImageContainer.scss";
import { connect } from "react-redux";
import Preview from "./Preview/Preview";
const ImageContainer = ({ lookBookItems }) => {
  const [preview, previewHandler] = useState({ isActive: false, currentImage: "" });

  return (
    <>
      <div className='lookbook-images'>
        <div className='lookbook-images-container container'>
          {lookBookItems.map((img, index) => {
            return (
              <div className='image-wrap' onClick={() => previewHandler({ isActive: true, currentImage: img.img })}>
                <img src={img.img} key={index} alt='lookbook' style={{ marginTop: `${index % 2 !== 0 ? "80px" : "20px"}` }} />
                <div className='image-wrap-info' style={{ top: `${index % 2 !== 0 ? "80px" : "20px"}` }}>
                  <p>Modelo: {img.models}</p>
                  <p>Fotografo: GianLyfe</p>
                </div>
              </div>
            );
          })}
        </div>
      </div>
      <Preview previewConfig={preview} previewHandler={previewHandler} />
    </>
  );
};

const mapStateToProps = state => {
  return {
    lookBookItems: state.general.lookBookItems
  };
};

export default connect(mapStateToProps)(ImageContainer);
