import React, { useState } from "react";
import "./Showcase.scss";
import { connect } from "react-redux";
import Item from "../../../components/Item/Item";
import { Link } from "react-router-dom";

const Showcase = ({ itemList }) => {
  const [isActive, isActiveHandler] = useState(false);
  console.log(itemList);
  return (
    <section className='showcase' onMouseEnter={() => isActiveHandler(true)} onMouseLeave={() => isActiveHandler(false)}>
      <div className={`button-showcase-container ${isActive ? "active" : ""}`}>
        <Link to='/store'>
          <div className='button-header'>STORE</div>
        </Link>
      </div>
      <div className='showcase-container container'>
        {itemList &&
          itemList.map((item, index) => {
            return index <= 2 ? <Item item={item} /> : null;
          })}
      </div>
      {/* <div className='showcase-container container'>
        {itemList &&
          itemList.map(item => {
            return <Item item={item} />;
          })}
      </div> */}
    </section>
  );
};

const mapStateToProps = state => {
  console.log(state);
  return {
    itemList: state.general.itemList
  };
};

export default connect(mapStateToProps)(Showcase);
