import React, { useState, useEffect } from "react";
import { Formik, Field, Form } from "formik";
import * as Yup from "yup";
import "./InfoForm.scss";
import MercadoPago from "../../../assets/img/mercadopago-01-e1562863464953.png";
import CreditCards from "../../../assets/img/credit-card-icons.png";
import Input from "../../../components/UI/INPUT/input";
import Button from "../../../components/UI/BUTTONS/button";
import Progress from "../../../components/UI/PROGRESS/progress";
import { connect } from "react-redux";
import { loadingHandler, calculateZipCode, setChooseSendValue, payToMercadoPago } from "../../../store/actions";

const nameRegex = /^[a-zA-Z ]*$/;
const dniRegex = /^[0-9]*$/;
const webSiteRegex = /(^[a-zA-Z_-]{3})([.]{1})([a-zA-Z0-9_.+-]+)([.]{1})([a-zA-Z_-]+)/;
const telRegex = /[0-9+]/;

const companySchema = Yup.object().shape({
  name: Yup.string()
    .matches(nameRegex, "Ingrese un formato vlido")
    .required("El campo es requerido."),

  lastName: Yup.string()
    .matches(nameRegex, "Ingrese un formato vlido")
    .required("El campo es requerido."),

  addressNumber: Yup.string().required("El campo es requerido."),

  zipCode: Yup.string().required("El campo es requerido."),

  lastName: Yup.string()
    .matches(nameRegex, "Ingrese un formato vlido")
    .required("El campo es requerido."),

  tel: Yup.string()
    .matches(telRegex, "Introduzca un formato vlido")
    .required("El campo es requerido")
    .max(20, "Ingrese un telefono vlido"),

  addressNumber: Yup.string()
    .matches(dniRegex, "Introduzca un formato vlido")
    .required("El campo es requerido"),

  address: Yup.string().required("El campo es requerido"),

  email: Yup.string()
    // .matches(webSiteRegex, "Introduzca un formato vlido")
    .required("El campo es requerido")
});

const InfoForm = ({ isLoading, calculateZipCode, setChooseSendValue, loadingHandler, sendValue, payToMercadoPago, cartItems, ...props }) => {
  //CONFIGURACION STATE
  let sendPrice = "$450";
  const [progress, setProgress] = useState("33%");
  const [currentStep, currentStepHandler] = useState(1);
  const [sendType, setSendType] = useState("");
  const [initialValues, initialValuesHandler] = useState({
    name: "",
    lastName: "",
    email: "",
    tel: "",
    address: "",
    addressNumber: "",
    addressOptional: "",
    addressApart: "",
    zipCode: "",
    locality: "",
    province: "Buenos Aires",
    sameSendValues: false
  });
  // useEffect(() => {
  //   valorhandler(sendValue);
  // });

  //FUNCION CAMBIO DE STEP
  const continueHandler = (step, progress) => {
    currentStepHandler(step);
    setProgress(progress);
  };

  //PASOS
  const firstStep = () => {
    return (
      <>
        <Formik initialValues={initialValues} validationSchema={companySchema}>
          {({ handleSubmit, isValid, touched, values, isSubmitting, errors }) => (
            <>
              <Form style={{ width: "100%" }}>
                <h4>INFORMACION DE CONTACTO</h4>

                <div className='multi-input-item'>
                  <label htmlFor='email' className='label mt0'>
                    EMAIL
                  </label>
                  <Field placeholder='tumail@email.com' type='email' name='email' component={Input} style={{ borderColor: `${errors.email ? "red" : "#d9d9d9"}` }} />
                </div>

                <div className='multi-input-item'>
                  <label htmlFor='tel' className='label'>
                    TELEFONO
                  </label>
                  <Field placeholder='112345423' type='tel' name='tel' component={Input} style={{ borderColor: `${errors.tel ? "red" : "#d9d9d9"}` }} />
                </div>

                <h4>INFORMACION DE ENVIO</h4>
                <div className='multi-input'>
                  <div className='multi-input-item'>
                    <label htmlFor='name' className='label mt0'>
                      NOMBRE
                    </label>
                    <Field placeholder='Tu nombre' type='text' name='name' component={Input} style={{ borderColor: `${errors.name ? "red" : "#d9d9d9"}` }} />
                  </div>
                  <div className='multi-input-item'>
                    <label htmlFor='lastName' className='label mt0'>
                      APELLIDO
                    </label>
                    <Field placeholder='Tu Apellido' type='text' name='lastName' component={Input} style={{ borderColor: `${errors.lastName ? "red" : "#d9d9d9"}` }} />
                  </div>
                </div>

                <div className='multi-input'>
                  <div className='multi-input-item'>
                    <label htmlFor='address' className='label'>
                      CALLE
                    </label>
                    <Field placeholder='Av. J.B Justo' type='text' name='address' component={Input} style={{ borderColor: `${errors.address ? "red" : "#d9d9d9"}` }} />
                  </div>
                  <div className='multi-input-item'>
                    <label htmlFor='addressNumber' className='label'>
                      NUMERO
                    </label>
                    <Field placeholder='123' type='text' name='addressNumber' component={Input} style={{ borderColor: `${errors.addressNumber ? "red" : "#d9d9d9"}` }} />
                  </div>
                </div>

                <div className='multi-input'>
                  <div className='multi-input-item'>
                    <label htmlFor='addressOptional' className='label'>
                      CASA, PISO, APARTAMENTO (opcional)
                    </label>
                    <Field placeholder='1 A' type='text' name='addressOptional' component={Input} />
                  </div>
                  <div className='multi-input-item'>
                    <label htmlFor='zipCode' className='label'>
                      CODIGO POSTAL {sendValue === "INVALIDO" ? <span style={{ color: "red" }}>*CP INVALIDO</span> : null}
                    </label>
                    <Field placeholder='7752' type='text' name='zipCode' component={Input} style={{ borderColor: `${errors.zipCode ? "red" : "#d9d9d9"}` }} />
                  </div>
                </div>

                <div className='multi-input'>
                  <div className='multi-input-item'>
                    <label htmlFor='locality' className='label'>
                      LOCALIDAD
                    </label>
                    <Field placeholder='CAPITAL FEDERAL' type='text' name='locality' component={Input} style={{ borderColor: `${errors.locality ? "red" : "#d9d9d9"}` }} />
                  </div>
                  <div className='multi-input-item'>
                    <label htmlFor='province' className='label'>
                      PROVINCIA
                    </label>
                    <Field placeholder='CAPITAL FEDERAL' type='text' name='province' component={Input} disabled />
                  </div>
                </div>

                <div className='multi-input-item checkbox'>
                  <label htmlFor='sameSendValues' className='label-radios'>
                    <div className={`checkbox-label ${values.sameSendValues ? "active" : ""}`}></div>
                    <span>USAR COMO DOMICILIO DE FACTURACION</span>
                  </label>
                  <Field name='sameSendValues' id='sameSendValues' className='checkbox-input' placeholder='CAPITAL FEDERAL' type='checkbox' checked={values.sameSendValues} />
                </div>

                <Button
                  onClick={async () => {
                    console.log("isValid ? ", isValid);
                    if (isValid) {
                      calculateZipCode(values.zipCode);
                      initialValuesHandler(values);
                      continueHandler(2, "66%");
                    } else {
                    }
                  }}
                >
                  CONTINUAR
                </Button>
              </Form>
            </>
          )}
        </Formik>
      </>
    );
  };

  const secondStep = () => {
    console.log("SEND", sendValue);
    if (sendValue === "INVALIDO") {
      continueHandler(1, "33%");
    } else {
      return (
        <>
          <h4 style={{ marginBottom: "10px" }}>ENVIO</h4>
          <h5>SELECCIONA EL TIPO DE ENVIO</h5>
          <Formik initialValues={{ sendType: "" }}>
            {({ values, setFieldValue }) => (
              <>
                <Form style={{ width: "100%" }}>
                  <div className='multi-input-item radios'>
                    <label htmlFor='a' className='label-radios'>
                      <div className={`checkbox-label ${values.sendType === "RETIRO" ? "active" : ""}`}></div>
                      <span>
                        PUNTO DE RETIRO EN RAMOS MEJIA (LUEGO DE REALIZAR LA COMPRA, NOS COMUNICAREMOS PARA COORDINAR ENTREGA) - <span style={{ fontWeight: "800" }}>GRATIS</span>
                      </span>
                    </label>
                    <input className='radio' type='radio' id='a' name='a' value='RETIRO' checked={values.sendType === "RETIRO"} onChange={() => setFieldValue("sendType", "RETIRO")} />
                  </div>
                  <div className='multi-input-item radios m-b'>
                    <label htmlFor='b' className='label-radios'>
                      <div className={`checkbox-label  ${values.sendType === "MERCADOENVIOS" ? "active" : ""}`}></div>
                      <span>
                        ENVIO ESTANDAR - <span style={{ fontWeight: "800" }}>{sendValue}</span>
                      </span>
                    </label>
                    <input className='radio' type='radio' name='b' id='b' value='MERCADOENVIOS' checked={values.sendType === "MERCADOENVIOS"} onChange={() => setFieldValue("sendType", "MERCADOENVIOS")} />
                  </div>
                  <Button
                    onClick={() => {
                      if (values.sendType === "") {
                        return;
                      } else {
                        setChooseSendValue(values.sendType === "RETIRO" ? "GRATIS" : `$${sendValue}`);
                        setSendType(values.sendType);
                        continueHandler(3, "100%");
                      }
                    }}
                  >
                    CONTINUAR
                  </Button>{" "}
                </Form>
              </>
            )}
          </Formik>
        </>
      );
    }
  };

  const thirdStep = () => {
    return (
      <div className='finish'>
        <div className='finish-status'>
          <div className='finish-status-item'>
            <span className='finish-status-item--type fijo'>CONTACTO</span>
            <span className='finish-status-item--value'>{initialValues.email}</span>
            <span className='finish-status-item--type' style={{ cursor: "pointer" }} onClick={() => continueHandler(1, "33%")}>
              CAMBIAR
            </span>
          </div>
          <div className='finish-status-item'>
            <span className='finish-status-item--type fijo'>ENVIAR A</span>
            <span className='finish-status-item--value'>{`${initialValues.address} ${initialValues.addressNumber}${initialValues.addressOptional !== "" ? `, ${initialValues.addressOptional}` : ""}, CP ${initialValues.zipCode}`}</span>
            <span className='finish-status-item--type' style={{ cursor: "pointer" }} onClick={() => continueHandler(1, "33%")}>
              CAMBIAR
            </span>
          </div>
          <div className='finish-status-item'>
            <span className='finish-status-item--type fijo'>METODO</span>
            <span className='finish-status-item--value ml3'>{sendType}</span>
            <span className='finish-status-item--type' style={{ cursor: "pointer" }} onClick={() => continueHandler(2, "66%")}>
              CAMBIAR
            </span>
          </div>
        </div>

        <h4 style={{ marginBottom: "10px" }}>PAGO</h4>
        <h5>TODAS LAS TRANSACCIONES SON SEGURAS Y ESTAN CIFRADAS</h5>

        <div className='finish-redirect'>
          <div className='finish-redirect--heading'>
            <div className='finish-redirect-ml'>
              <img src={MercadoPago} />
            </div>
            <div className='finish-redirect-list-container'>
              <div className='finish-redirect-list'>
                <img src={CreditCards} />
              </div>
              <span>y mas..</span>
            </div>
          </div>
          <div className='finish-redirect--body'>
            <span>AL FINALIZAR EL PEDIDO, SERAS REDIRIGIDO A MERCADO PAGO PARA COMPLETAR TU COMPRA DE FORMA SEGURA</span>
          </div>
        </div>

        <Button onClick={() => payToMercadoPago(cartItems, initialValues, sendType)}>FINALIZAR PEDIDO</Button>
      </div>
    );
  };

  const stepHandler = () => {
    if (currentStep === 1) {
      return firstStep();
    } else if (currentStep === 2) {
      return secondStep();
    } else if (currentStep === 3) {
      return thirdStep();
    } else {
      return;
    }
  };
  //className={`${touched.name && values.name === "" && errors.name ? "isNotValid" : ""}${touched.name && values.name && !errors.name ? "isValid" : ""}`}
  return (
    <div className='info-form'>
      <div className='info-form-group'>
        <Progress width={progress} />
        {stepHandler()}
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    isLoading: state.cart.isLoading,
    sendValue: state.cart.sendValue,
    cartItems: state.general.cartItems
  };
};
export default connect(mapStateToProps, { loadingHandler, calculateZipCode, setChooseSendValue, payToMercadoPago })(InfoForm);
