import React, { useEffect } from "react";
import Nav from "../../components/Nav/Nav";
import Footer from "../../components/Footer/Footer";
import "./statusPage.scss";
const statusPage = ({ type, ...props }) => {
  console.log(props);

  setTimeout(() => {
    props.history.push("/");
  }, 5000);

  const successType = () => {
    return;
  };

  const pendType = () => {};

  const failType = () => {};
  let fail = "Algo salio mal. ";
  let success = "Tu pago fue acreditado correctamente.";
  let pending = "Tu pago se encuentra pendiente, una vez acreditado, nos comunicaremos contigo.";

  let statusMensaje;
  if (type === "success" || type === "pend") {
    statusMensaje = "!Exito!";
  } else {
    statusMensaje = "Error";
  }

  let statusBody;
  if (type === "success") {
    statusBody = success;
  } else if (type === "pend") {
    statusBody = pending;
  } else {
    statusBody = fail;
  }

  let errorComponent = <div></div>;
  return (
    <>
      <Nav />
      <div className='status-page'>
        <div className='status-page-container'>
          <h1>{statusMensaje}</h1>
          <h2>{statusBody}</h2>

          <h4>Estas siendo redirigido a la pagina principal..</h4>
        </div>
      </div>
      <Footer />;
    </>
  );
};

export default statusPage;
