import React from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { sendToItemView } from "../../store/actions";
import "./Item.scss";
import { motion } from "framer-motion";
const transition = { duration: 0.5, ease: [0.43, 0.13, 0.23, 0.96] };

const thumbnailVariants = {
  initial: { scale: 0.9, opacity: 0 },
  enter: { scale: 1, opacity: 1, transition },
  exit: {
    scale: 0.5,
    opacity: 0,
    transition: { duration: 1.5, ...transition },
  },
};

const frameVariants = {
  hover: { scale: 0.95 },
};

const imageVariants = {
  hover: { scale: 1.1 },
};

const Item = ({ item, history, sendToItemView }) => {
  const front = item.itemPicture.front;
  const back = item.itemPicture.back;
  console.log("item", item);
  console.log("item", item);
  return (
    <motion.div
      variants={thumbnailVariants}
      className='item'
      onClick={() => {
        sendToItemView(item);
        history.push(`/item/${item._id}`);
      }}
    >
      <div className='item-image-container'>
        <motion.img variants={imageVariants} transition={transition} src={front} onMouseOver={(e) => (e.currentTarget.src = back)} onMouseOut={(e) => (e.currentTarget.src = front)} />
      </div>
      <h4 className='description'>
        {item.name} - ${item.price}
      </h4>
    </motion.div>
  );
};

export default withRouter(connect(null, { sendToItemView })(Item));
