import React from "react";
import "./Header.scss";
import { Link } from "react-router-dom";
const Header = () => {
  return (
    <header className='home'>
      <div className='home-header container'>
        <Link to='/store'>
          <div className='button-header'>SHOP SS/2020</div>
        </Link>
      </div>
    </header>
  );
};

export default Header;
