import React from "react";
import "./ResumenItem.scss";
import { connect } from "react-redux";
const ResumenItem = ({ chooseSendValue, cartItems, showCartValue, ...props }) => {
  const renderItem = (item, index) => {
    return (
      <div className='resumen-item-item'>
        <div className='resumen-item-item--img'>
          <img src={item.itemPicture.front} />
        </div>
        <div className='resumen-item-item--info'>
          <h4>{item.name.toUpperCase()}</h4>
          <p>
            {item.color.toUpperCase()} / {item.size}
          </p>
          <p>CANT:{item.quantity}</p>
          {/* <p className='cart-item--info__price'>
            {item.price} - {item.size} - CANTIDAD {item.quantity} -{item.color.toUpperCase()}
          </p>
         */}
        </div>
        <div className='resumen-item-item--actions'>
          <p>${item.price}</p>
        </div>
      </div>
    );
  };

  return (
    <div className='resumen-item'>
      <h4>RESUMEN</h4>
      <div className='resumen-item-values'>
        <div>SUBTOTAL</div>
        <div>1000</div>
      </div>
      <div className='resumen-item-values'>
        <div>ENVIO</div>
        <div>{chooseSendValue}</div>
      </div>
      <div className='resumen-item-total'>
        <h5>TOTAL</h5>
        <h5>1000</h5>
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    chooseSendValue: state.cart.chooseSendValue,
    cartItems: state.general.cartItems,
    showCartValue: state.general.showCart
  };
};

export default connect(mapStateToProps)(ResumenItem);
