import React, { useState, useEffect } from "react";
import "./LookbookPreview.scss";
import { Link } from "react-router-dom";
const LookbookPreview = () => {
  const [currentImage, currentImageHandler] = useState(1);
  const [isActive, isActiveHandler] = useState(false);
  setTimeout(() => {
    let currentValue;
    if (currentImage === 1) {
      currentValue = 2;
    } else if (currentImage === 2) {
      currentValue = 3;
    } else if (currentImage === 3) {
      currentValue = 1;
    }

    currentImageHandler(currentValue);
  }, 3000);

  let background;

  if (currentImage === 1) {
    background = "https://res.cloudinary.com/dvmll0ruo/image/upload/c_scale,w_2000/v1582160399/lookbook/7V9A3101_a23j4o.jpg";
  } else if (currentImage === 2) {
    background = "https://res.cloudinary.com/dvmll0ruo/image/upload/c_scale,w_2000/v1582161885/lookbook/7V9A3643_gxeaec.png";
  } else if (currentImage === 3) {
    background = "https://res.cloudinary.com/dvmll0ruo/image/upload/c_scale,w_2000/v1582160481/lookbook/7V9A3381_rwvm0v.jpg";
  }

  return (
    <div className='lookbook-preview' onMouseEnter={() => isActiveHandler(true)} onMouseLeave={() => isActiveHandler(false)}>
      <div className={`button-preview-container ${isActive ? "active" : ""}`}>
        <Link to='/lookbook'>
          <div className='button-header'>LOOKBOOK</div>
        </Link>
      </div>
      <div className='lookbook-preview--image' style={{ backgroundImage: `url(${background})` }}></div>
    </div>
  );
};

export default LookbookPreview;
