import React, { useEffect } from "react";
import { connect } from "react-redux";
import { addItemQuantity, removeItemQuantity, removeItemCart, showCart, calculatePrice } from "../../store/actions";
import { Link } from "react-router-dom";
import "./Cart.scss";
import Button from "../UI/BUTTONS/button";
import { ReactComponent as Close } from "../../assets/svg/close.svg";
const Cart = ({ cartItems, addItemQuantity, removeItemQuantity, showCartValue, removeItemCart, showCart, totalPrice, calculatePrice }) => {
  useEffect(() => {
    calculatePrice();
  });
  console.log(cartItems);
  const subTotalPrice =
    cartItems.length > 0
      ? cartItems.reduce(function(total, currentValue) {
          return total + currentValue.price;
        }, 0)
      : null;

  console.log(subTotalPrice);

  const renderEmpty = () => {};

  const renderItem = (item, index) => {
    return (
      <div className='cart-item'>
        <div className='cart-item--img'>
          <img src={item.itemPicture.front} />
        </div>
        <div className='cart-item--info'>
          <h4>{item.name.toUpperCase()}</h4>
          <p>
            {item.color.toUpperCase()} / {item.size}
          </p>
          <p>
            <span onClick={() => removeItemQuantity(item.itemId, index)}>-</span> {item.quantity} <span onClick={() => addItemQuantity(item.itemId, index)}> +</span>
          </p>
          {/* <p className='cart-item--info__price'>
            {item.price} - {item.size} - CANTIDAD {item.quantity} -{item.color.toUpperCase()}
          </p>
         */}
        </div>
        <div className='cart-item--actions'>
          <p>${item.price}</p>
          <div onClick={() => removeItemCart(item.itemId, item.size, index)} className='cart-item--info__delete'>
            ELIMINAR
          </div>
        </div>
      </div>
    );
  };

  return (
    <>
      <div className={`overlay-cart ${showCartValue ? "active" : ""}`} onClick={showCart}></div>
      <div className={`cart ${showCartValue ? "active" : ""}`}>
        <div className='close-cart' onClick={showCart}>
          <Close />
        </div>
        {cartItems.length > 0 ? (
          <>
            {cartItems.map((item, index) => renderItem(item, index))}

            <div className='cart-price-info'>
              <div className='cart-price-info__subtotal'>
                <h4 className='carty-price-info--el'>SUBTOTAL</h4>
                <h4 className='carty-price-info--el'>${totalPrice}</h4>
              </div>
            </div>

            <div className='cart-price__actions'>
              <Link to='/paymentform' onClick={showCart}>
                <Button isReverse style={{ marginBottom: "14px" }}>
                  COMPRAR
                </Button>
              </Link>
              <Link to='/store' onClick={showCart}>
                <Button>SEGUIR COMPRANDO</Button>
              </Link>
            </div>
          </>
        ) : (
          <div className='empty'>
            <h2>CARRITO VACIO</h2>
            <Link to='/store' onClick={showCart}>
              <Button style={{ marginBottom: "14px" }}>IR AL STORE</Button>
            </Link>
          </div>
        )}
      </div>
    </>
  );
};

const mapStateToProps = state => {
  return {
    cartItems: state.general.cartItems,
    showCartValue: state.general.showCart,
    totalPrice: state.general.totalPrice
  };
};

export default connect(mapStateToProps, { addItemQuantity, removeItemQuantity, removeItemCart, showCart, calculatePrice })(Cart);
