import React, { useState } from "react";
import "./SideNav.scss";
import { ReactComponent as Close } from "../../../assets/svg/close.svg";
import { Link } from "react-router-dom";
const SideNav = ({ sideNavActive, setSideNavActive }) => {
  console.log(sideNavActive);
  const [isClicked, isClickedHandler] = useState(false);
  return (
    <div className={`side-nav ${sideNavActive}`}>
      <div className='side-nav-container'>
        <Link to='/'>
          <h2>HOME</h2>
        </Link>
        <Link to='/store'>
          <h2>SEASON 2020</h2>
        </Link>
        {/* <h2 onClick={() => isClickedHandler(!isClicked)}>CATEGORIAS</h2>
        <div className={`category ${isClicked ? "active" : ""}`}>
          <Link to='/category/shirt'>REMERAS</Link>
          <Link to='/category/hoodie'>BUZOS</Link>
        </div> */}
        <Link to='/lookbook'>
          <h2>LOOKBOOK</h2>
        </Link>
        <Close onClick={() => setSideNavActive("disabled")} />
      </div>
    </div>
  );
};

export default SideNav;
