import React, { Component } from "react";
import Nav from "../../components/Nav/Nav";
import Main from "./Main/Main";
import Footer from "../../components/Footer/Footer";

class FormPage extends Component {
  componentDidMount() {
    window.scrollTo({
      top: 0
    });
  }
  render() {
    return (
      <>
        <Nav />
        <Main />
        <Footer />
      </>
    );
  }
}

export default FormPage;
