import React, { useState, useEffect } from "react";
import "./Nav.scss";
import { ReactComponent as Burger } from "../../assets/svg/menu-button.svg";
import { ReactComponent as Search } from "../../assets/svg/search.svg";
import { ReactComponent as CartSVG } from "../../assets/svg/shopping-bag.svg";
import { ReactComponent as User } from "../../assets/svg/user.svg";
import { Link } from "react-router-dom";
import SideNav from "./SideNav/SideNav";
import Cart from "../../components/Cart/Cart";
import { connect } from "react-redux";
import { showCart } from "../../store/actions/index";
import FloatingNav from "./FloatingNav/FloatingNav";
const Nav = props => {
  const [scroll, setScroll] = useState(false);
  const [sideNavActive, setSideNavActive] = useState("");
  const [isCartHovered, cartHandler] = useState(false);
  useEffect(() => {
    document.addEventListener("scroll", () => {
      const scrollCheck = window.scrollY > 2;
      // console.log(window.scrollY)
      if (scrollCheck !== scroll) {
        setScroll(scrollCheck);
      } else {
        setScroll(false);
      }
    });
  }, []);

  console.log(props);
  return (
    <nav className={`nav ${scroll && props.isHome ? "scrolled" : ""} ${!props.isHome ? "scrolled" : ""} ${props.dark ? "dark" : ""}`}>
      {/* <div className={`backdrop ${isCartHovered ? "active" : ""}`}></div> */}
      <SideNav sideNavActive={sideNavActive} setSideNavActive={setSideNavActive} />
      <div className='nav-container'>
        {/* <FloatingNav scroll={scroll} /> */}
        <Cart />
        <Burger onClick={() => setSideNavActive("active")} />
        <Link to='/'>
          <h1 className='title'>DIRTY DEMON</h1>
        </Link>

        <div>
          {/* <Search style={{ width: "20px", height: "20px" }} />

          <User style={{ width: "20px", height: "20px", marginLeft: "30px" }} /> */}
          <CartSVG onClick={() => props.showCart()} />
        </div>
      </div>
    </nav>
  );
};

const mapStateToProps = state => {
  return {};
};
export default connect(null, { showCart })(Nav);
