import * as actionTypes from "../actions/types";
import { ITEM, ITEMLIST } from "../../models/items";
const INITIAL_STATE = {
  isAuth: false,
  currentItem: null,
  cartItems: [],
  showCart: false,
  isLoading: false,
  sendValue: "",
  chooseSendValue: "A CALCULAR",
  totalPrice: 0
};

const addItemToCartHandler = () => {};

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case actionTypes.GET_ITEMS:
      return {
        ...state
      };
    case actionTypes.ADD_ITEM_CART:
      return {
        ...state,
        cartItems: state.cartItems.concat(action.payload),
        showCart: true
      };
    case actionTypes.REMOVE_ITEM_CART:
      console.log("ID:", action.payload);
      console.log("ID:", state.cartItems);
      return {
        ...state,
        cartItems: state.cartItems.filter(item => {
          return item.size !== action.payload.size;
        })
      };
    case actionTypes.ADD_ITEM_QUANTITY:
      return {
        ...state,
        cartItems: state.cartItems.map(item => {
          if (item.id === action.payload) {
            return { ...item, quantity: item.quantity + 1 };
          } else {
            return item;
          }
        })
      };
    case actionTypes.REMOVE_ITEM_QUANTITY:
      return {
        ...state,
        cartItems: state.cartItems.map(item => {
          if (item.id === action.payload && item.quantity > 1) {
            return { ...item, quantity: item.quantity - 1 };
          } else {
            return item;
          }
        })
      };

    case actionTypes.SHOW_CART:
      return {
        ...state,
        showCart: !state.showCart
      };
    case actionTypes.SET_SEND_VALUE:
      console.log("PAYLOAD", action.payload);
      return {
        ...state,
        sendValue: action.payload
      };
    case actionTypes.SET_CHOOSE_SEND_VALUE:
      return {
        ...state,
        chooseSendValue: action.payload
      };
    default:
      return state;
  }
};
