import React from "react";
import "./Preview.scss";
const Preview = ({ previewConfig, previewHandler, ...props }) => {
  const { isActive, currentImage } = previewConfig;
  return (
    <div className={`preview ${isActive ? "active" : ""}`} onClick={() => previewHandler({ isActive: false, currentImage: "" })}>
      <div className='preview-image'>
        <img src={currentImage} alt='preview' />
      </div>
    </div>
  );
};
export default Preview;
