import * as actionTypes from "../actions/types";
import axios from "../../axios";

export const sendToItemView = (item) => {
  return {
    type: actionTypes.SEND_TO_ITEM_VIEW,
    payload: item,
  };
};

export const getPaymentsFromML = () => {
  return (dispatch) => {
    axios.get("https://api.mercadolibre.com/sites/MLA/payment_methods?marketplace=NONE&operation_type=recurring_payment").then((response) => {
      dispatch({
        type: actionTypes.GET_PAY_METHODS,
        payload: response.data,
      });
    });
  };
};

export const getItems = () => {
  return (dispatch) => {
    axios({
      method: "get",
      url: "/items",
      headers: {
        Authorization: "Basic DD2020616",
      },
    })
      .then((response) => {
        console.log(response.data.data);
        dispatch({ type: actionTypes.GET_ITEMS, payload: response.data.data });
      })
      .catch((error) => {
        console.log(error.response);
      });
  };
};

export const getItem = (id, history) => {
  return (dispatch) => {
    axios({
      method: "get",
      url: `/items/${id}`,
      headers: {
        Authorization: "Basic DD2020616",
      },
    })
      .then((response) => {
        console.log(response.data.data);
        dispatch(sendToItemView(response.data.data));
      })
      .catch((error) => {
        console.log(error.response);
        history.push("/store");
      });
  };
};
