import React from "react";
import "./Resume.scss";
import { connect } from "react-redux";
import { addItemQuantity, removeItemQuantity, payToMercadoPago } from "../../../store/actions/index";
import PayMethods from "../PayMethods/PayMethods";
const Resume = ({ cartItems, addItemQuantity, removeItemQuantity, payToMercadoPago, ...props }) => {
  const tableItem = (item, index) => {
    console.log(item);
    return (
      <tr className='table-item'>
        <td className='table-item--img'>
          <img src={item.itemPicture.front} alt='img' />
        </td>
        <td className='table-item--desc'>
          <div>
            <p>DIRTY DEMON</p>
            <p>{item.name.toUpperCase()}</p>
            <p>TALLE {item.size}</p>
          </div>
        </td>
        <td className='table-item--price'>{item.price}</td>
        <td className='table-item--cant'>{item.quantity}</td>
        <td className='table-item--total'>
          <span onClick={() => removeItemQuantity((item.itemId, index))}>- </span>$10<span onClick={() => addItemQuantity(item.itemId, index)}> +</span>
        </td>
        <td className='table-item--delete'>x</td>
      </tr>
    );
  };
  return (
    <div className='checkout-resume'>
      <div className='table-container'>
        <table>
          <thead>
            <tr>
              <th>DESCRIPCION</th>
              <th></th>
              <th>PRECIO</th>
              <th>CANT</th>
              <th>TOTAL</th>
              <th>ELIMINAR</th>
            </tr>
          </thead>
          <tbody>{cartItems.map((item, index) => tableItem(item, index))}</tbody>
        </table>
      </div>
      {/* <button onClick={() => payToMercadoPago()}>PAGAR</button> */}
      <PayMethods />
    </div>
  );
};

const mapStateToProps = state => {
  return {
    cartItems: state.general.cartItems
  };
};
export default connect(mapStateToProps, { addItemQuantity, removeItemQuantity, payToMercadoPago })(Resume);
