import React, { useEffect } from "react";
import "./PayMethods.scss";
import { connect } from "react-redux";
import { getPaymentsFromML } from "../../../store/actions";

const PayMethods = ({ payMethods, getPaymentsFromML, ...props }) => {
  useEffect(() => {
    getPaymentsFromML();
  }, []);
  console.log(payMethods);
  return (
    <div className='paymethods'>
      <div>
        <p>Dirty Demon acepta</p>
        <div className='paymethods-cards-container'>
          {payMethods &&
            payMethods.map(card => {
              return <img src={card.secure_thumbnail} style={{ maxWidth: "30px" }} />;
            })}
        </div>
      </div>
      <p>Todas las transacciones e informaciones sobre la tarjeta de crdito utilizan productos de comunicacin SSL / TLS, garanta de una comunicacin siempre codificada</p>
      <p>Contctenos o consulte el servicio al cliente y para obtener ms informacin</p>
    </div>
  );
};
const mapStateToProps = state => {
  return {
    payMethods: state.general.payMethods
  };
};
export default connect(mapStateToProps, { getPaymentsFromML })(PayMethods);
